import * as styles from "../../styles/shopping.module.css";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
    toggleItemInCart,
    updateItemQuantity,
    selectCart,
} from "../../state/onlineStoreSlice";
import ItemDetailModal from "./itemDetailModal";

const ItemList = () => {
    const dispatch = useDispatch();
    const cart = useSelector(selectCart);
    const storeItems = useSelector((state) => state.onlineStore.storeItems);

    const [selectedItem, setSelectedItem] = useState(null);
    const [isSingleColumn, setIsSingleColumn] = useState(false);

    // Check for single-column layout using a media query
    useEffect(() => {
        const checkColumnLayout = () => {
            const element = document.querySelector(`.${styles.itemList}`);
            if (element) {
                const computedStyle = getComputedStyle(element);
                const gridTemplateColumns = computedStyle.getPropertyValue("grid-template-columns");
                setIsSingleColumn(gridTemplateColumns.split(" ").length === 1);
            }
        };

        checkColumnLayout();
        window.addEventListener("resize", checkColumnLayout);

        return () => {
            window.removeEventListener("resize", checkColumnLayout);
        };
    }, []);

    const handleToggle = (item) => {
        dispatch(toggleItemInCart(item));
    };

    const handleQuantityChange = (item, quantity) => {
        dispatch(updateItemQuantity({ id: item.id, quantity: parseInt(quantity, 10) }));
    };

    const isInCart = (id) => cart.some((item) => item.id === id);

    const handleItemClick = (item) => {
        if (!isSingleColumn) {
            setSelectedItem(item); // Only allow modal if not single-column
        }
    };

    const handleCloseModal = () => {
        setSelectedItem(null);
    };

    return (
        <div className={styles.itemList}>
            {storeItems.map((item, index) => {
                const itemInCart = cart.find((cartItem) => cartItem.id === item.id);
                const quantity = itemInCart ? itemInCart.quantity : 1;

                return (
                    <div
                        key={index}
                        className={styles.item}
                        onClick={() => handleItemClick(item)}
                    >
                        <img
                            src={item.image}
                            alt={item.name}
                            className={styles.itemImage}
                            width={100}
                            height={100}
                        />
                        <h3>{item.name}</h3>
                        <p>{item.description}</p>
                        <p>${item.price}</p>
                        <p># Remaining: {item.stock}</p>

                        {isInCart(item.id) ? (
                            <button
                                onClick={(e) => {
                                    e.stopPropagation();
                                    handleToggle(item);
                                }}
                                className={styles.removeButton}
                            >
                                Remove from Cart
                            </button>
                        ) : (
                            <button
                                onClick={(e) => {
                                    e.stopPropagation();
                                    handleToggle(item);
                                }}
                                disabled={item.stock <= 0}
                            >
                                Add to Cart
                            </button>
                        )}

                        {isInCart(item.id) && (
                            <div>
                                <label>Quantity: </label>
                                <input
                                    type="number"
                                    value={quantity}
                                    onChange={(e) => handleQuantityChange(item, e.target.value)}
                                    min="1"
                                    max={item.stock}
                                    onClick={(e) => e.stopPropagation()}
                                />
                            </div>
                        )}
                    </div>
                );
            })}

            {!isSingleColumn && selectedItem && (
                <ItemDetailModal
                    item={selectedItem}
                    onClose={handleCloseModal}
                    handleToggle={handleToggle}
                    handleQuantityChange={handleQuantityChange}
                    cart={cart}
                    isInCart={isInCart}
                />
            )}
        </div>
    );
};

export default ItemList;
