import * as styles from "../../styles/shopping.module.css";
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { selectCart, updateItemQuantity, toggleItemInCart } from '../../state/onlineStoreSlice';

const ViewCart = () => {
    const dispatch = useDispatch();
    const cart = useSelector(selectCart);

    const handleQuantityChange = (item, quantity) => {
        const newQuantity = Math.max(1, Math.min(quantity, item.stock)); // Limit between 1 and stock
        dispatch(updateItemQuantity({ id: item.id, quantity: newQuantity }));
    };

    const handleRemoveItem = (item) => {
        dispatch(toggleItemInCart(item)); // Use toggle to remove item
    };

    if (!cart || cart.length === 0) {
        return <p className={styles.emptyCartMessage}>Your cart is empty!</p>;
    }

    return (
        <div className={styles.cart}>
            <h2>Your Cart</h2>
            <table className={styles.cartTable}>
                <thead>
                    <tr>
                        <th>Item</th>
                        <th>Price</th>
                        <th>Quantity</th>
                        <th>Total</th>
                        <th>Remove</th>
                    </tr>
                </thead>
                <tbody>
                    {cart.map((item, index) => {
                        const price = Number(item.price) || 0;
                        const quantity = Number(item.quantity) || 0;
                        const total = price * quantity;

                        return (
                            <tr key={index}>
                                <td>{item.name || "Unnamed Item"}</td>
                                <td>${price.toFixed(2)}</td>
                                <td>
                                    <input
                                        type="number"
                                        value={quantity}
                                        onChange={(e) => handleQuantityChange(item, parseInt(e.target.value, 10))}
                                        min="1"
                                        max={item.stock}
                                    />
                                </td>
                                <td>${total.toFixed(2)}</td>
                                <td>
                                    <button
                                        onClick={() => handleRemoveItem(item)}
                                        className={styles.removeButton}
                                    >
                                        Remove
                                    </button>
                                </td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
            <h3 className={styles.grandTotal}>
                Grand Total: $
                {cart.reduce(
                    (total, item) =>
                        total + (Number(item.price) || 0) * (Number(item.quantity) || 0),
                    0
                ).toFixed(2)}
            </h3>
        </div>
    );
};

export default ViewCart;
