import * as styles from "../../styles/shopping.module.css";
import React, { useState, useEffect } from "react";

const ItemDetailModal = ({
    item,
    onClose,
    handleToggle,
    handleQuantityChange,
    isInCart,
    cart,
}) => {
    const itemInCart = cart.find((cartItem) => cartItem.id === item.id); // Get item from cart
    const initialQuantity = itemInCart ? itemInCart.quantity : 1; // Default to 1 if not in cart

    const [quantity, setQuantity] = useState(initialQuantity); // Set initial quantity from cart

    const handleQuantityUpdate = (e) => {
        const newQuantity = Math.min(Math.max(parseInt(e.target.value, 10), 1), item.stock); // Clamp to valid range
        setQuantity(newQuantity);
        handleQuantityChange(item, newQuantity);
    };

    useEffect(() => {
        const handleKeyDown = (event) => {
            if (event.key === "Escape") {
                onClose(); // Close modal on Escape key
            }
        };

        // Add event listener
        document.addEventListener("keydown", handleKeyDown);

        // Cleanup listener on unmount
        return () => {
            document.removeEventListener("keydown", handleKeyDown);
        };
    }, [onClose]);

    return (
        <div className={styles.fullScreenModal} onClick={onClose}>
            <div
                className={styles.modalContent}
                onClick={(e) => e.stopPropagation()} // Prevent closing on modal content click
            >
                <button className={styles.closeButton} onClick={onClose}>
                    &times; Close
                </button>
                <img
                    src={item.image}
                    alt={item.name}
                    className={styles.fullScreenImage}
                />
                <h3>{item.name}</h3>
                <p>{item.description}</p>
                <p>Price: ${item.price}</p>
                <p>Stock: {item.stock}</p>

                {isInCart(item.id) && (
                    <div>
                        <label htmlFor="quantityInput">Quantity: </label>
                        <input
                            id="quantityInput"
                            type="number"
                            value={quantity}
                            onChange={handleQuantityUpdate}
                            min="1"
                            max={item.stock}
                        />
                    </div>
                )}

                <button
                    onClick={() => handleToggle(item)}
                    className={isInCart(item.id) ? styles.removeButton : ""}
                    disabled={item.stock <= 0}
                >
                    {isInCart(item.id) ? "Remove from Cart" : "Add to Cart"}
                </button>
            </div>
        </div>
    );
};

export default ItemDetailModal;
