import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import StoreItems from './storeItems';
import ShoppingCart from './cart';
import {
    toggleCartView,
    selectCartVisibility,
    updateStoreItems,
    cleanCart,
} from '../../state/onlineStoreSlice';
import config from '../../../config';
import * as styles from "../../styles/shopping.module.css";

const fetchSpreadsheetData = async (sheetName) => {
    const url = `https://docs.google.com/spreadsheets/d/${config.SPREADSHEET_ID_PRODUCTS}/gviz/tq?tqx=out:csv&sheet=${sheetName}`;
    try {
        const response = await fetch(url);
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }
        const csvText = await response.text();
        return parseCSV(csvText);
    } catch (error) {
        console.error(`Error fetching spreadsheet data from sheet "${sheetName}":`, error);
        return [];
    }
};

const parseCSV = (csv) => {
    const rows = csv.trim().split("\n");
    const headers = rows.shift().split(",").map(h => h.replace(/(^"|"$)/g, ""));
    return rows.map(row => {
        const values = row.split(",").map(value => value.replace(/(^"|"$)/g, ""));
        const obj = {};
        headers.forEach((header, index) => {
            if (header) {
                obj[header] = values[index] || null;
            }
        });
        return obj;
    });
};

const ShoppingPage = () => {
    const dispatch = useDispatch();
    const isCartVisible = useSelector(selectCartVisibility);

    useEffect(() => {
        const loadItems = async () => {
            const data = await fetchSpreadsheetData(config.PRODUCTS_SHEETNAME);
            dispatch(updateStoreItems(data));
            dispatch(cleanCart()); // Validate cart after store items update
        };

        loadItems();
    }, [dispatch]);

    const handleToggleCart = () => {
        dispatch(toggleCartView());
    };

    return (
        <div>
            <h1>Shopping Page</h1>
            <button className={styles.cartToggleButton} onClick={handleToggleCart}>
                {isCartVisible ? 'Hide Cart' : 'View Cart'}
            </button>
            {isCartVisible && <ShoppingCart />}
            <div className={styles.itemListContainer}>
                <StoreItems />
            </div>
        </div>
    );
};

export default ShoppingPage;
